import React, { useState, useEffect } from "react";
import Error from "../ErrorMessage.js";
import * as Yup from "yup";
import { TextField } from "material-ui-formik-components/TextField";
import { Select } from "material-ui-formik-components/Select";
import { ErrorMessage, Formik, Form, Field } from "formik";
import {
  Grid,
  Typography,
  Paper,
  Button,
  MenuItem,
  LinearProgress,
  CircularProgress,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import useForm from "../utils/useForm";
import SygefexMuiInput from "../muiComponents/controls/SygefexMuiInput";
import SygefexMuiSelect from "../muiComponents/controls/SygefexMuiSelect";
import { useMutation, useApolloClient, useQuery } from "@apollo/react-hooks";
import {
  getDivisionsOfARegionQuery,
  getAllRegionsQuery,
} from "../queries&Mutations&Functions/Queries";
import { getObjectFromID } from "../queries&Mutations&Functions/Functions";
import { createSubDivisionMutation } from "../queries&Mutations&Functions/Mutations";

const useStyles = makeStyles({
  root: {
    display: "flex",
    flexDirection: "column",
    // fontSize: 100,
  },
  divStyled: {
    display: "grid",
    placeItems: "center",
    top: "2rem",
    height: "90vh",
  },
  pageStyled: {
    display: "grid",
    placeItems: "center",
    marginTop: "2rem",
    padding: "1rem",
    minWidth: "30vw",
  },
  listStyled: {
    display: "grid",
    placeItems: "center",
    listStyleType: "none",
    margin: 0,
    padding: 0,
    paddingTop: "0.1rem",
  },
  titleStyled: {
    display: "grid",
    placeItems: "center",
  },

  allControls: {
    display: "grid",
    placeItems: "center",
    paddingTop: "0.2rem",
    border: "0.05rem solid #1254ac",
    // width: "20vw",
    borderRadius: "0.5rem",
    // marginTop: "2rem",
  },
  centerAll: {
    display: "grid",
    placeItems: "center",
    minWidth: "30vw",
  },
});

const validationSchema = Yup.object().shape({
  region: Yup.object().required("Choissisez une région"),
  division: Yup.string().required("Choissisez un département"),
  subDivName: Yup.string().required("Nom arrondissement Obligatoire"),
  subDivCode: Yup.string().required("Code arrondissement Obligatoire"),
});

const NewSubDivision = () => {
  const classes = useStyles();
  const client = useApolloClient();
  const [regions, setRegions] = useState([]);
  const [state, setState, handleInputChange] = useForm({ regionID: "" });

  const initialValues = {
    subDivName: "",
    subDivCode: "",
    division: "",
    region: "",
  };
  const loadRegionData = async () => {
    const { error, data, loading } = await client.query({
      query: getAllRegionsQuery,
    });
    console.log(data.regions);
    {
      loading && <LinearProgress />;
    }
    setRegions(data.regions);
  };

  useEffect(() => {
    loadRegionData();
  }, []);
  console.log(state.regionID);
  // const refinedRegion = removeTypename(regions)
  const getRegionsOptions =
    regions && regions.map((item) => ({ value: item, label: item.regName }));

  const { data: dataRegion, loadingRegion, errRegion } = useQuery(
    getDivisionsOfARegionQuery,
    {
      skip: !state.regionID,
      variables: { id: state.regionID },
    }
  );
  {
    loadingRegion && <CircularProgress />;
  }
  console.log(dataRegion);
  const getRegion = dataRegion && dataRegion.region;
  const { division } = { ...getRegion };
  console.log(division);
  const getDivOptions =
    division &&
    division.map((item) => ({ value: item, label: item.subDivName }));

  const [createSubDivision, { loading, error }] = useMutation(
    createSubDivisionMutation,
    {
      refetchQueries: ["getAllSubDivisionsQuery"],
    }
  );

  return (
    <Formik
      method="POST"
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={async (values, { setSubmitting, resetForm }) => {
        const res = await createSubDivision({
          variables: {
            ...values,
            division: values.division && getObjectFromID(values.division.id),
          },
        });
        setTimeout(() => {
          console.log(JSON.stringify(values, null, 2));
          console.log(res);
          resetForm(true);
          setSubmitting(false);
        }, 400);
      }}
    >
      {({ submitForm, setFieldValue, isSubmitting }) => {
        return (
          <div className={classes.centerAll}>
            <Paper className={classes.pageStyled} elevation={3}>
              <Form aria-busy={isSubmitting}>
                {(isSubmitting || loading) && <LinearProgress />}
                <Grid className={classes.centerAll} container>
                  <Grid container className={classes.centerAll}>
                    <Grid item>
                      <Error error={error} />
                      <Typography
                        className={classes.titleStyled}
                        variant="body1"
                      >
                        Nouvel Arrondissement
                      </Typography>
                    </Grid>
                  </Grid>
                  <Grid container className={classes.centerAll}>
                    <Grid item className={classes.centerAll}>
                      <Field
                        variant="standard"
                        name="region"
                        label="La Région"
                        onChange={handleInputChange}
                        component={Select}
                        placeholder="la Région"
                        disabled={isSubmitting}
                        helperText={<ErrorMessage name="region" />}
                        options={getRegionsOptions}
                        fullWidth
                      />
                      <Field
                        variant="standard"
                        name="division"
                        label="Le Département"
                        component={Select}
                        placeholder="le Département"
                        disabled={isSubmitting}
                        helperText={<ErrorMessage name="division" />}
                        options={getRegionsOptions}
                        fullWidth
                      />

                      <SygefexMuiInput
                        name="subDivName"
                        type="text"
                        label="Nom Arrondissement"
                        fullWidth
                        variant="standard"
                        disabled={isSubmitting || loading}
                        helperText={<ErrorMessage name="subDivName" />}
                      />
                      <SygefexMuiInput
                        name="subDivCode"
                        type="text"
                        label="Code Arrondissement"
                        fullWidth
                        variant="standard"
                        disabled={isSubmitting || loading}
                        helperText={<ErrorMessage name="subDivCode" />}
                      />
                      <Button disabled={isSubmitting} onClick={submitForm}>
                        Valid{isSubmitting ? "ation en cours" : "er"}
                      </Button>
                    </Grid>
                  </Grid>
                </Grid>
              </Form>
            </Paper>
          </div>
        );
      }}
    </Formik>
  );
};
export default NewSubDivision;
